import axios from "axios"

export const login = (email, password) => dispatch => {
    axios.post('/auth/loginWithEmail', {
        email,
        password
    })
        .then(({ data }) => {
            console.log(data)
            const token = `Bearer ${data.token}`
            
            axios.defaults.headers.common['Authorization'] = token
            localStorage.setItem('token', data.token)
            dispatch({
                type: 'LOGIN',
                authedUser: 'admin'
            })
            window.location.reload()
        })
}

export const logout = () => dispatch => {    
    axios.defaults.headers.common['Authorization'] = ''
    localStorage.removeItem('token')

    dispatch({
        type: 'LOGOUT',
    })
}

export const getUsers = () => dispatch => {
    axios.get('/admin/users/getUsers')
        .then(({ data }) => {
            dispatch({
                type: 'SET_USERS',
                users: data
            })
        })
}

export const getUser = (uid) => dispatch => {
    axios.post('/admin/users/getUser', { uid })
        .then(({ data }) => {
            dispatch({
                type: 'SET_USER',
                user: data
            })
        })
}

export const deleteUser = (uid) => dispatch => {
    axios.delete('/admin/users/deleteUser', { data: { uid } })
        .then((_) => {
            dispatch({
                type: 'REMOVE_USER',
                uid
            })
        })
}

