import React from 'react'

const Drivers = React.lazy(() => import('./views/drivers/Drivers'))
const Driver = React.lazy(() => import('./views/drivers/Driver'))
const Messages = React.lazy(() => import('./views/messages/Messages'))
const Message = React.lazy(() => import('./views/messages/Message'))
const Trips = React.lazy(() => import('./views/trips/Trips'))
const Trip = React.lazy(() => import('./views/trips/Trip'))
const Users = React.lazy(() => import('./views/users/Users'))
const User = React.lazy(() => import('./views/users/User'))

const routes = [
  { path: '/', exact: true, name: 'Home' },
  { path: '/home', name: 'Users', component: Users },
  { path: '/users', exact: true,  name: 'Users', component: Users },
  { path: '/users/:id', exact: true, name: 'User Details', component: User },
  { path: '/drivers', exact: true, name: 'Drivers', component: Drivers },
  { path: '/drivers/:id', exact: true, name: 'Driver Details', component: Driver },
  { path: '/trips', exact: true, name: 'Trips', component: Trips },
  { path: '/trips/:id', exact: true, name: 'Trip Details', component: Trip },
  { path: '/messages', exact: true, name: 'Messages', component: Messages },
  { path: '/messages/:id', exact: true, name: 'Message', component: Message }
]

export default routes
